import React from 'react';
import { graphql } from 'gatsby';
import Img from 'gatsby-image';
import { Query } from '../../graphql-types';
import { Layout } from '../components/layout';
import { SEO } from '../components/seo';
import { Separator } from '../components/separator';
import { BreadCrumbs } from '../components/breadcrumbs';
import Container from 'react-bootstrap/Container';
import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';
import { FormattedMessage, Link, useIntl } from 'gatsby-plugin-intl';

const RefurbishedPage: React.FunctionComponent<{ data: Query }> = ({ data }) => {
  const intl = useIntl();
  const { allFile } = data;
  const ref_a = allFile.edges.find((v) => v.node.name === 'ref_a').node.childImageSharp.fluid;
  const ref_b = allFile.edges.find((v) => v.node.name === 'ref_b').node.childImageSharp.fluid;
  const ref_c = allFile.edges.find((v) => v.node.name === 'ref_c').node.childImageSharp.fluid;

  function renderContactUs() {
    return (
      <Container>
        <Row>
          <Col>
            <Link className="btn btn-primary" to="/contact">
              <strong>Contact us</strong>
            </Link>
          </Col>
        </Row>
      </Container>
    );
  }
  function renderContent(locale: string) {
    switch (locale) {
      case 'fr':
        return (
          <Container>
            <Row>
              <Separator />
              <Col>
                <h1>ÉQUIPEMENTS AÉROPORTUAIRES RÉNOVÉS ET ATTENTES</h1>
                <p className="text-justify">
                  Combien de temps pensez-vous que vos équipements aéroportuaires rénovés fonctionneront? Une année? Une décennie? Vous
                  attendez-vous à une qualité quasi parfaite? L'achat est-il la meilleure option? Souhaitez-vous bénéficier davantage de la
                  location? Il y a beaucoup de questions à considérer quand il s'agit d'équipement aéroportuaire rénové. C'est pourquoi
                  Aviaco discute les facteurs importants avec vous. En plus de vos attentes, nous vous assurons que votre budget, vos
                  exigences de performance et vos délais sont tous respectés. Vous pouvez choisir un équipement dans un état «tel quel».
                  Vous pouvez choisir la rénovation «Zero time». Et vous voudrez peut-être savoir ce que nous recommandons pour votre
                  situation particulière. Dans tous les cas, votre équipement arrivera avec une garantie de trois mois ou plus. C’est votre
                  garantie d’une rénovation de qualité et d’un service de qualité.
                </p>
              </Col>
            </Row>

            <hr />

            <Row>
              <Col>
                <div className="image-column-left">
                  <Img fluid={ref_a} className="img-fluid" alt="A range of maintenance inspections for GSE" />
                </div>
                <h2>UNE GAMME DE CONTRÔLES DE MAINTENANCE POUR GSE</h2>
                <p className="text-justify">
                  Aviaco effectue différents services de maintenance programmée pour ses clients. Ils sont basés sur les recommandations
                  spécifiques de vos machines et équipements aéroportuaires, y compris les recommandations du fabricant pour les différentes
                  marques et modèles. Cependant, nous nous adaptons et y ajoutons également. Cela garantit que vos conditions spécifiques
                  sont prises en compte, qu'elles soient liées au climat, à la législation, à votre environnement, à la fréquence
                  d'utilisation ou à tout autre facteur important spécifique à vous et à votre aéroport. Nos expériences de rénovation et de
                  réparation de GSE d'occasion nous ont permis de mieux comprendre les problèmes qui surviennent sans un entretien adéquat.
                  Nous voyons de première main à quel point il est important d'empêcher ces problèmes de se produire. Chaque étape que nous
                  faisons, tout ce que nous vérifions et observons, et chaque correction ou remplacement que nous effectuons est
                  soigneusement documenté. C’est la meilleure façon de vous rassurer. Nous vous informons de toute action future que nous
                  pensons être nécessaire, vous fournissons des conseils ciblés et vous informons de la date de votre prochaine visite de
                  service. En d'autres termes? Nous vous apportons la tranquillité d'esprit.
                </p>
              </Col>
            </Row>

            <hr />

            <Row>
              <Col>
                <h2>APERÇU DE LA RÉNOVATION</h2>
                <p className="text-justify">
                  Nous avons des normes élevées en ce qui concerne la qualité que nous livrons. Avec une rénovation «temps zéro» complète,
                  l’équipement aéroportuaire est remis en excellent état de fonctionnement. Nous nous attendons à ce que l’équipement
                  continue à performer pendant encore au moins dix à quinze ans. Ceci est rendu possible en y consacrant du temps et des
                  efforts. Et mener une série de tests différents et effectuer toutes les tâches de rénovation que nous jugeons appropriées.
                  Ils comprennent l'inspection et l'entretien de tous les composants et systèmes pertinents et le remplacement des pièces
                  usées:
                </p>
              </Col>
              <Col>
                <div className="image-column-right">
                  <Img fluid={ref_b} className="img-fluid" alt="Refurbishment overview" />
                </div>
              </Col>
            </Row>
            <Row>
              <Col>
                <p className="text-justify">
                  <strong>Mécanique</strong>
                  <ul>
                    <li>
                      Une révision complète du moteur ou des composants du moteur, selon leur état. Nous effectuerons également des
                      remplacements complets de moteurs pour nous assurer que les moteurs sont conformes aux exigences en matière
                      d'émissions.
                    </li>
                    <li>Une révision complète de la transmission.</li>
                    <li>
                      Des tests de pression hydraulique. Nous renouvelons les composants et tuyaux, refermons les vérins hydrauliques, les
                      moteurs et les pompes.
                    </li>
                    <li>
                      Recâblage des pièces et composants électriques. Cela peut inclure n'importe quoi, du rebobinage des générateurs au
                      reconditionnement des cartes de circuits imprimés.
                    </li>
                    <li>Des tests de refroidissement pour les unités de climatisation / chauffage (ACU).</li>
                    <li>Reconditionnement des systèmes de refroidissement des climatiseurs.</li>
                  </ul>
                  <strong>Travail du corps</strong>
                  <ul>
                    <li>Ponçage complet et repeinture, intérieur et extérieur.</li>
                    <li>Rénovation intérieure.</li>
                  </ul>
                </p>
              </Col>
              <Col>
                <p className="text-justify">
                  <strong>Tests spécifiques aux équipements</strong>
                  <ul>
                    <li>Tests de bancs de charge pour les unités d'alimentation au sol (GPU); Capacités 400 Hz CA et 28 V CC.</li>
                    <li>Tests de cône pour les unités de démarrage pneumatique (ASU); tuyaux simples, doubles ou triples.</li>
                  </ul>
                  <strong>Documentation</strong>
                  <ul>
                    Remise de documents spécifiques.
                    <ul>
                      <li>Inspections d'entretien signées et approuvées.</li>
                      <li>Manuels de maintenance pour le GSE spécifique.</li>
                      <li>Manuels d'opération.</li>
                      <li>Manuels de pièces de rechange.</li>
                      <li>Schémas électriques et hydrauliques, etc.</li>
                      <li>Transport et chargement des documents.</li>
                    </ul>
                  </ul>
                </p>
              </Col>
            </Row>

            <hr />

            <Row>
              <Col>
                <Img fluid={ref_c} className="img-fluid" alt="Experts in refurbished ground support equipment" />
              </Col>
              <Col>
                <h3>EXPERTS EN ÉQUIPEMENT DE SOUTIEN AU SOL RÉNOVÉ</h3>
                <p className="text-justify">
                  Toutes les rénovations sont effectuées par nos propres techniciens dans nos ateliers principaux aux Pays-Bas et en
                  Espagne. Ils sont bien formés, expérimentés et comprennent exactement ce qu'implique la révision des équipements
                  aéroportuaires. Nos experts sont au cœur de nos services de maintenance. Ils se feront également un plaisir de vous aider
                  à vérifier et à maintenir votre GSE dans des conditions optimales, vous aidant à prolonger la durée de vie de vos machines
                  et à continuer à fournir des services de haute qualité.
                </p>
              </Col>
            </Row>
            <hr />
            <Row>
              <Col>
                <h3>LIVRÉ À VOTRE PORTE</h3>
                <p className="text-justify">
                  Lorsque vous achetez chez Aviaco, votre équipement aéroportuaire rénové est livré à votre porte. Aviaco possède une vaste
                  expérience et un réseau de contacts fiables dans le monde de la logistique, vous donnant accès à des options de livraison
                  qui répondent à vos besoins en matière de budget et de calendrier.
                </p>
              </Col>
            </Row>
            <Row>
              <Col>
                <h3>DES QUESTIONS AU SUJET DE LES ÉQUIPEMENT AÉROPORTUAIRES RÉNOVÉS?</h3>
                <p className="text-justify">
                  Vous souhaitez en savoir plus sur nos équipements aéroportuaires rénovés? Ou comment effectuons-nous nos rénovations?
                  Contactez-nous!
                  <br />
                  Nous sommes impatients de vous aider.
                </p>
              </Col>
            </Row>
          </Container>
        );
      case 'es':
        return (
          <Container>
            <Row>
              <Separator />
              <Col>
                <h1>GSE REACONDICIONADO Y EXPECTATIVAS</h1>
                <p className="text-justify">
                  Cuánto tiempo espera que funcione su GSE reacondicionado? ¿Un año? ¿Una década? ¿Espera una calidad casi perfecta?
                  ¿Comprar es la mejor opción? ¿Se beneficiaría más del alquiler? Hay muchas preguntas a considerar cuando se trata de
                  equipos de apoyo en tierra reacondicionados. Es por eso por lo que Aviaco analiza los factores importantes con usted.
                  Además de sus expectativas, nos aseguramos de que se cumplan su presupuesto, requisitos de desempeño y plazos. Puede
                  elegir el equipo en una condición "tal cual está". Puede elegir la restauración "casi nuevo". Y es posible que desee saber
                  qué recomendamos para sus circunstancias específicas. En cualquier caso, su equipo llegará con una garantía de tres meses
                  o más. Este es su seguro de una reforma y un servicio de máxima calidad.
                </p>
              </Col>
            </Row>

            <hr />

            <Row>
              <Col>
                <div className="image-column-left">
                  <Img fluid={ref_a} className="img-fluid" alt="A range of maintenance inspections for GSE" />
                </div>
                <h2>UNA GAMA DE SERVICIOS DE MANTENIMIENTO PARA SU GSE</h2>
                <p className="text-justify">
                  Aviaco realiza diferentes servicios de mantenimiento programado para clientes. Estos se basan en las recomendaciones
                  específicas para sus equipos de apoyo en tierra, incluidas las recomendaciones del fabricante para diferentes marcas y
                  modelos. Sin embargo, también nos adaptamos y agregamos valor añadido a estos servicios. Esto garantiza que tengamos en
                  cuenta sus condiciones específicas, ya sean relacionadas con el clima, la legislación, el medio ambiente, la frecuencia de
                  uso o cualquier otro factor importante específico para usted y su aeropuerto. Nuestra experiencia en la restauración y
                  reparación de GSE de segunda mano nos ha dado una idea de los problemas que surgen sin un mantenimiento adecuado. Vemos de
                  primera mano lo importante que es evitar que surjan estos problemas. Cada paso que damos, todo lo que verificamos y
                  observamos, y cada corrección o reemplazo que hacemos se documenta cuidadosamente. Le informamos de cualquier acción
                  futura que esperamos pueda ser necesaria, le brindamos asesoramiento específico y le informamos cuándo será su próxima
                  visita de servicio. ¿En otras palabras? Le damos tranquilidad.
                </p>
              </Col>
            </Row>

            <hr />

            <Row>
              <Col>
                <h2>REACONDICIONAMIENTO – VISION GENERAL</h2>
                <p className="text-justify">
                  Tenemos altos estándares en lo que respecta a la calidad que ofrecemos. Con un reacondicionamiento completo "casi nuevo",
                  el equipo de apoyo en tierra se devuelve a una condición excelente y útil. Esperamos que funcione durante al menos otros
                  diez o quince años. Esto es posible al dedicar tiempo y esfuerzo. Y realizar una serie de pruebas diferentes y las tareas
                  de renovación que consideremos adecuadas. Estas incluyen la inspección y el mantenimiento de todos los componentes y
                  sistemas relevantes y el reemplazo de cualquier pieza desgastada:
                </p>
              </Col>
              <Col>
                <div className="image-column-right">
                  <Img fluid={ref_b} className="img-fluid" alt="Refurbishment overview" />
                </div>
              </Col>
            </Row>
            <Row>
              <Col>
                <p className="text-justify">
                  <strong>Mecánica</strong>
                  <ul>
                    <li>
                      Una revisión completa del motor o de los componentes del motor, según su estado. También realizaremos reemplazos
                      totales del motor para garantizar que estos cumplan con los requisitos de emisiones.
                    </li>
                    <li>Una revisión completa de la transmisión.</li>
                    <li>
                      Pruebas de presión hidráulica. Renovamos mangueras y componentes hidráulicos, volvemos a sellar cilindros hidráulicos,
                      motores y bombas.
                    </li>
                    <li>
                      Recableado de piezas y componentes eléctricos. Esto puede incluir cualquier cosa, desde rebobinar generadores hasta
                      reacondicionar placas de circuito impreso.
                    </li>
                    <li>Pruebas de refrigeración para unidades de aire acondicionado / calefacción (ACUs).</li>
                    <li>Reacondicionamiento de sistemas de refrigeración de aire acondicionado.</li>
                  </ul>
                  <strong>Carrocería</strong>
                  <ul>
                    <li>Lijado completo y repintado, interior y exterior.</li>
                    <li>Reacondicionamiento interior.</li>
                  </ul>
                </p>
              </Col>
              <Col>
                <p className="text-justify">
                  <strong>Pruebas específicas</strong>
                  <ul>
                    <li>Pruebas de banco de carga para GPUs; capacidades de 400 Hz CA y 28 V CC.</li>
                    <li>Pruebas de cono para ASUs; mangueras simples, dobles o triples.</li>
                  </ul>
                  <strong>Documentación</strong>
                  <ul>
                    Entrega de documentos específicos.
                    <ul>
                      <li>Inspecciones de mantenimiento firmadas y aprobadas.</li>
                      <li>Manuales de mantenimiento específicos de cada GSE</li>
                      <li>Manuales de operación.</li>
                      <li>Manuales de repuestos.</li>
                      <li>Esquemas eléctricos e hidráulicos, etc.</li>
                      <li>Documentos de Transporte y carga</li>
                    </ul>
                  </ul>
                </p>
              </Col>
            </Row>

            <hr />

            <Row>
              <Col>
                <Img fluid={ref_c} className="img-fluid" alt="Experts in refurbished ground support equipment" />
              </Col>
              <Col>
                <h3>EXPERTOS EN REACONDICIONAMIENTO DE GSE</h3>
                <p className="text-justify">
                  Todas las reformas son realizadas por nuestros propios técnicos en nuestros principales talleres en Holanda y España.
                  Ellos están bien capacitados, tienen experiencia y comprenden exactamente lo que implica la revisión de equipos de apoyo
                  en tierra (GSE). Nuestros expertos son el núcleo de nuestros servicios de mantenimiento. También le ayudarán con gusto a
                  comprobar y mantener su GSE en óptimas condiciones, ayudándole a alargar la vida útil de sus equipos y seguir
                  proporcionando servicios de alta calidad.
                </p>
              </Col>
            </Row>
            <hr />
            <Row>
              <Col>
                <h3>ENTREGA EN SUS INSTALACIONES</h3>
                <p className="text-justify">
                  Cuando compra en Aviaco, su GSE reacondicionado se entrega en sus instalaciones. Aviaco tiene una amplia experiencia y una
                  red de contactos fiables en el mundo de la logística, lo que le brinda acceso a opciones de entrega que satisfacen sus
                  necesidades de presupuesto y tiempo
                </p>
              </Col>
            </Row>
            <Row>
              <Col>
                <h3>PREGUNTAS ACERCA DE GSE REACONDICIONADO?</h3>
                <p className="text-justify">
                  Le gustaría saber más sobre nuestro equipo de apoyo en tierra reacondicionado? cómo realizamos nuestras reformas?
                  <br />
                  Estamos deseando ayudarle.
                </p>
              </Col>
            </Row>
          </Container>
        );
      default:
        return (
          <Container>
            <Row>
              <Separator />
              <Col>
                <h1>Refurbished Ground Support Equipment and expectations</h1>
                <p className="text-justify">
                  How long do you expect your refurbished ground support equipment to perform? A year? A decade? Do you expect near-perfect
                  quality? Is buying the best option? Would you benefit more from renting? There are plenty of questions to consider when it
                  comes to refurbished ground support equipment. This is why Aviaco discusses the important factors with you. As well as
                  your expectations, we make sure your budget, performance requirements and deadlines are all met. You might choose
                  equipment in an ‘As is’ condition. You might choose ‘Zero time’ refurbishment. And you may want to know what we recommend
                  for your specific circumstances. Whatever the case, your equipment will arrive with a warranty of three months or longer.
                  It’s your guarantee of a top-quality refurbishment and quality service.
                </p>
              </Col>
            </Row>

            <hr />

            <Row>
              <Col>
                <div className="image-column-left">
                  <Img fluid={ref_a} className="img-fluid" alt="A range of maintenance inspections for GSE" />
                </div>
                <h2>A range of maintenance inspections for GSE</h2>
                <p className="text-justify">
                  Aviaco performs different scheduled maintenance services for clients. They are based on the specific recommendations for
                  your machines and ground support equipment, including the manufacturer recommendations for different brands and models.
                  However, we also adapt and add to these. This ensures your specific conditions are taken into account, whether they relate
                  to climate, legislation, environment, frequency of use, or any other important factor specific to you and your airport.
                  Our experiences refurbishing and repairing second-hand GSE has given us insight into the problems that arise without
                  adequate maintenance. We see first-hand how important it is to stop these problems from arising. Every step we take,
                  everything we check and observe, and every correction or replacement we make is carefully documented. It’s the best way to
                  set your mind at ease. We inform you of any future actions we expect may be necessary, provide targeted advice and let you
                  know when your next service visit will be due. In other words? We give you peace of mind.
                </p>
              </Col>
            </Row>

            <hr />

            <Row>
              <Col>
                <h2>Refurbishment overview</h2>
                <p className="text-justify">
                  We have high standards when it comes to the quality we deliver. With a full ‘Zero time’ refurbishment, ground service
                  equipment is returned to excellent, serviceable condition. We expect it to perform for at least another ten to fifteen
                  years. This is made possible by putting in the time and effort. And conducting an array of different tests and performing
                  any refurbishment tasks we believe to be appropriate. They include inspecting and servicing all relevant components and
                  systems and replacing any worn parts:
                </p>
              </Col>
              <Col>
                <div className="image-column-right">
                  <Img fluid={ref_b} className="img-fluid" alt="Refurbishment overview" />
                </div>
              </Col>
            </Row>
            <Row>
              <Col>
                <p className="text-justify">
                  <strong>Mechanical</strong>
                  <ul>
                    <li>
                      A full overhaul of the engine or the engine components, depending on their condition. We will also perform total
                      engine replacements to ensure engines comply with emission requirements.
                    </li>
                    <li>A full overhaul of the transmission.</li>
                    <li>
                      Hydraulic pressure tests. We renew hydraulic components and hoses, reseal hydraulic cylinders, motors and pumps.{' '}
                    </li>
                    <li>
                      Rewiring of electrical parts and components. This may include anything from rewinding generators to reconditioning
                      printed circuit boards.
                    </li>
                    <li>Cooling tests for Air Conditioning/Heating Units (ACUs).</li>
                    <li>Reconditioning of air conditioner cooling systems.</li>
                  </ul>
                  <strong>Body work</strong>
                  <ul>
                    <li>Full sanding and respraying, inside and outside. </li>
                    <li>Interior reconditioning.</li>
                  </ul>
                </p>
              </Col>
              <Col>
                <p className="text-justify">
                  <strong>Equipment specific tests</strong>
                  <ul>
                    <li>Load-bank tests for Ground Power Units (GPUs); 400 Hz AC &amp; 28 V DC capabilities.</li>
                    <li>Cone tests for Air Start Units (ASUs); single, double or triple hoses.</li>
                  </ul>
                  <strong>Documentation</strong>
                  <ul>
                    Delivery of specific documents.
                    <ul>
                      <li>Signed and approved maintenance inspections.</li>
                      <li>Maintenance manuals for the specific GSE.</li>
                      <li>Operation manuals.</li>
                      <li>Spare part manuals.</li>
                      <li>Electric and hydraulic schematics, etc.</li>
                      <li>Transport &amp; loading documents.</li>
                    </ul>
                  </ul>
                </p>
              </Col>
            </Row>

            <hr />

            <Row>
              <Col>
                <Img fluid={ref_c} className="img-fluid" alt="Experts in refurbished ground support equipment" />
              </Col>
              <Col>
                <h3>Experts in refurbished ground support equipment</h3>
                <p className="text-justify">
                  All refurbishments are performed by our own technicians at our main workshops in the Netherlands and Spain. They are
                  well-trained, experienced and understand exactly what is involved in overhauling GSE equipment. Our experts are the core
                  of our maintenance services. They will also gladly help you to check and keep your GSE in optimal condition, helping you
                  to extend the life of your machines and keep providing high-quality services.
                </p>
              </Col>
            </Row>
            <hr />
            <Row>
              <Col>
                <h3>Delivered to your door</h3>
                <p className="text-justify">
                  When you buy from Aviaco, your refurbished ground support equipment is delivered to your door. Aviaco has extensive
                  experience and a network of reliable contacts in the logistics world, giving you access to delivery options that meet your
                  budget and timing needs.
                </p>
              </Col>
            </Row>
            <Row>
              <Col>
                <h3>Questions about refurbished ground support equipment?</h3>
                <p className="text-justify">
                  Would you like to know more about our refurbished ground support equipment? Or how we perform our refurbishments?&nbsp;
                  <br />
                  We look forward to helping.
                </p>
              </Col>
            </Row>
          </Container>
        );
    }
  }

  return (
    <Layout>
      <SEO
        title={intl.formatMessage({ id: 'refurbished-seo-title' })}
        description={intl.formatMessage({ id: 'refurbished-seo-description' })}
        keywords={intl.formatMessage({ id: 'refurbished-seo-keyword' })}
      />

      <main role="main">
        <article>
          <header className="page-header">
            <Container>
              <Row>
                <Col>
                  <BreadCrumbs section="refurbished" />
                  <h1 className="page-title">
                    <FormattedMessage id="refurbished" />
                  </h1>
                  <p className="page-description">
                    <FormattedMessage id="refurbished-description" />
                  </p>
                  <Separator />
                </Col>
              </Row>
            </Container>
          </header>
          {renderContent(intl.locale)}
          {renderContactUs()}
        </article>
      </main>
    </Layout>
  );
};

export const query = graphql`
  {
    allFile(filter: { name: { in: ["ref_a", "ref_b", "ref_c"] } }) {
      edges {
        node {
          name
          childImageSharp {
            id
            fluid {
              ...GatsbyImageSharpFluid
            }
          }
        }
      }
    }
  }
`;

export default RefurbishedPage;
